import React from 'react';
import Container from '@material-ui/core/Container';
import WaterfallCard from '../Components/Waterfall card';
import { Typography } from '@material-ui/core';
import MaxWidthDialog from '../Components/Dialogue Water';
import DesertCard from '../Components/Desert';
import DeskCard from '../Components/Desk';
import { Grid } from '@material-ui/core';



export default function Home() {
  return (
    <React.Fragment>
        <Container fixed>
          <br/>
           <Typography><h1> My Portfolio </h1></Typography>
           <br/>
           <Grid container spacing={4}>
           <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
           <WaterfallCard/>
           </Grid>
           <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
           <DesertCard/>
           </Grid>
           <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
           <DeskCard/>
           </Grid>
          
           </Grid>
          
          
        </Container>
    </React.Fragment>
  );
}